.login-container {
  height: 300px;
  padding: 15px;
  display: grid;
  place-items: center;
}
.form-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
  width: 75%;
  background-color: var(--gris-nav);
  padding: 50px 20px 30px 20px;
  margin-top: 50px;
}
.passInput-container,
.emailInput-container,
.button-login {
  margin: 7px;
}
.email-label,
.password-label {
  margin-right: 5px;
  font-family: Archivo, sans-serif;
  font-size: 16px;
}
.email-input,
.password-input {
  height: 25px;
  border-radius: 4px;
  border: solid rgb(116, 116, 116) 1px;
}
.button-login {
  margin-top: 20px;
  height: 37px;
  width: 100px;
  border: none;
  border-radius: 6px;
  background-color: var(--icon-enabled);
  color: #fff;
  font-family: Archivo, sans-serif;
  font-size: 16px;
}
